const Layout = (props) => {

    // const [arrow, setArrow] = useState(<i className="fas fa-arrow-circle-left"></i>)

    // const {toggleKeypad, setToggleKeypad} = props;

    // const KeypadToggleFn = useCallback ((toggler) => {
    //     const numberPad = document.getElementById('numberPad');
    //     setToggleKeypad();
    //     if(toggler){
    //         numberPad.classList.remove(`${classNames.number_pad_container_shrink}`)
    //         numberPad.classList.add(`${classNames.number_pad_container_grow}`);
    //         setArrow(<i className="fas fa-arrow-circle-right"></i>)
    //     }else{
    //         numberPad.classList.remove(`${classNames.number_pad_container_grow}`);
    //         numberPad.classList.add(`${classNames.number_pad_container_shrink}`)
    //         setArrow(<i className="fas fa-arrow-circle-left"></i>)
    //     }
    // }, [setToggleKeypad])

    // useEffect(()=>{
    //     if(toggleKeypad){
    //         KeypadToggleFn(toggleKeypad)
    //     }
    // },[toggleKeypad, KeypadToggleFn])

    const {setToggleKeypad, keypadHide, toggleKeypad, keypadShow} = props;
    // const {setToggleKeypad, toggleKeypad} = props;

    useEffect(()=>{
        const numberPad = document.getElementById('numberPad');
        const keypadTrigger = document.getElementById('keypadTrigger');
        const board_container = document.getElementById('board_container');

        const keypadShowFn = () => {
            keypadShow();
        }

        const documentClickFn = (e) => {

            if (e.target !== numberPad && !numberPad.contains(e.target) && e.target !== keypadTrigger && !keypadTrigger.contains(e.target) && e.target !== board_container && !board_container.contains(e.target)) {
                if(toggleKeypad){
                    keypadHide();
                }
            }
        }

        document.addEventListener("click", documentClickFn);
        board_container.addEventListener('click', keypadShowFn);
        return () => {
            board_container.removeEventListener('click', keypadShowFn);
            document.removeEventListener("click", documentClickFn);
        };

    },[setToggleKeypad, keypadHide, toggleKeypad, keypadShow])

    // useEffect(()=>{
    //     const board_container = document.getElementById('board_container');
    //     const keypadShowFn = () => {
    //         store.dispatch(keypadShow());
    //     }
    //     board_container.addEventListener('click', keypadShowFn);
    //     return () => {
    //         board_container.removeEventListener('click', keypadShowFn);
    //     };
    // },[toggleKeypad])

    const keypadToggle = () => {
        // const numberPad = document.getElementById('numberPad');
        setToggleKeypad();

        // if(props.toggleKeypad){
        //     numberPad.classList.remove(`${classNames.number_pad_container_shrink}`)
        //     numberPad.classList.add(`${classNames.number_pad_container_grow}`);
        //     setArrow(<i className="fas fa-arrow-circle-right"></i>)
        // }else{
        //     numberPad.classList.remove(`${classNames.number_pad_container_grow}`);
        //     numberPad.classList.add(`${classNames.number_pad_container_shrink}`)
        //     setArrow(<i className="fas fa-arrow-circle-left"></i>)
        // }
    }

    // useEffect(()=>{
    //     document.addEventListener("click", (e) => {
    //         const numberPad = document.getElementById('numberPad');
    //         const board_container = document.getElementById('board_container');
    //         if (e.target !== numberPad && !numberPad.contains(e.target) && e.target !== board_container && !board_container.contains(e.target)) {
    //             keypadHide();
    //         }
    //         if (e.target !== board_container && !board_container.contains(e.target)) {
    //             console.log('Clicked')
    //             keypadShow();
    //         }
    //     });
    // },[keypadHide, keypadShow, toggleKeypad])

    const [icon, setIcon] = useState(<i className="fas fa-bars"></i>)

    const sideMenuToggle = () => {
        if(props.sideMenuToggle){
            setIcon(<i className="fas fa-bars"></i>)
        }else{
            setTimeout(() => {
                setIcon(<i className="fas fa-times"></i>)
            }, 300);
        }
        props.sideMenuToggleFn()
    }

    return (
        <>
            <div className={classNames.container}>
                <Header />
                <div className={classNames.inner}>
                    <div className={classNames.board_container} id='board_container'>
                        <Board/>
                    </div>
                    <div className={classNames.explainer_container}>
                        <ExplainationBoard/>
                    </div>
                    <div className={`${classNames.number_pad_container} ${props.toggleKeypad? classNames.number_pad_container_grow:classNames.number_pad_container_shrink}`} id='numberPad'>
                        <div className={classNames.keypadTrigger}>
                            <button onClick={keypadToggle}>
                                {props.toggleKeypad?
                                <i className="fas fa-arrow-circle-right"></i>:
                                <i className="fas fa-arrow-circle-left"></i>}
                            </button>
                        </div>
                        <Keypad/>
                    </div>
                </div>
                <Buttons/>
            </div>
            <div className={classNames.mvlogo}>
                <img src='https://zoomcbd.com/math/logo.svg' alt='Logo' />
            </div>
            <div className={classNames.mvmenu} onClick={()=>{sideMenuToggle()}}>
                {icon}
            </div>
            <Bar />
        </>
    );
};
const mapStatetoProps = state => {
    return{
        sideMenuToggle : state.numpad.sideMenuToggle,
        toggleKeypad : state.numpad.toggleKeypad
    }
}
const mapDispatchtoProps = dispatch => {
    return{
        setToggleKeypad: () => dispatch(reduxFunctions.keypadToggle()),
        keypadHide: () => dispatch(reduxFunctions.keypadHide()),
        keypadShow: () => dispatch(reduxFunctions.keypadShow()),
        sideMenuToggleFn: () => dispatch(reduxFunctions.sideMenuToggle())
    }
}
export default connect(mapStatetoProps, mapDispatchtoProps)(Layout);